import { reactive } from 'vue';
export default {
  name: 'VertexForm',
  props: {
    dialogVisible: {
      type: Boolean,
      default: false
    }
  },
  emits: ['update:dialogVisible', 'cancel', 'confirm'],

  setup(props, ctx) {
    const form = reactive({
      type: "Normal"
    });

    const reset = () => {
      form.type = "Normal";
    };

    const cancelHandle = () => {
      ctx.emit('update:dialogVisible', false);
      ctx.emit('cancel');
      reset();
    };

    const confirmHandle = () => {
      ctx.emit('update:dialogVisible', false);
      ctx.emit('confirm', form.type);
      reset();
    };

    return {
      form,
      cancelHandle,
      confirmHandle
    };
  }

};