export default {
  name: 'Buttons',

  data() {
    return {
      status: 0,
      titles: ["Add vertex", "Add edge", "Edit vertex", "Remove object", "Drag vertex", "Clear", "Save as JSON", "Upload JSON", "Download code"]
    };
  },

  methods: {
    clickHandle(id) {
      if (this.status != id) {
        this.status = id;
      } else {
        this.status = 0;
      }

      this.$emit("statusChanged", this.status);

      if (this.status == 6 || this.status == 7 || this.status == 8 || this.status == 9) {
        this.status = 0;
      }
    }

  }
};