import { reactive } from 'vue';
export default {
  name: 'EdgeForm',
  props: {
    dialogVisible: {
      type: Boolean,
      default: false
    }
  },
  emits: ['update:dialogVisible', 'cancel', 'confirm'],

  setup(props, ctx) {
    const form = reactive({
      input: '',
      output: '',
      bracket_list: []
    });

    const addList = () => {
      form.bracket_list.push({
        bracket_type: 'Open',
        bracket_number: '',
        bracket_label: ''
      });
    };

    const deleteList = () => {
      if (form.bracket_list.length > 0) {
        form.bracket_list.pop();
      }
    };

    const reset = () => {
      form.input = "";
      form.output = "";
      form.bracket_list = [];
    };

    const cancelHandle = () => {
      ctx.emit('update:dialogVisible', false);
      ctx.emit('cancel');
      reset();
    };

    const confirmHandle = () => {
      for (let i in form.bracket_list) {
        if (form.bracket_list[i].bracket_number == '') {
          alert('You must fill in the "bracket_number" field, which is a positive integer that determines the type of the bracket.');
          return;
        }
      }

      ctx.emit('update:dialogVisible', false);
      ctx.emit('confirm', form);
      reset();
    };

    return {
      form,
      addList,
      deleteList,
      cancelHandle,
      confirmHandle
    };
  }

};